@font-face {
  font-family: "NES-TTF";
  src: local("GoldmanBold"), url("./fonts/NES-TTF.ttf") format("truetype");
}

@font-face {
  font-family: "PixelMplus12";
  src: local("GoldmanBold"), url("./fonts/PixelMplus12-Regular.ttf") format("truetype");
}

html,
body {
  background-color: #00008b;
  color: white;
}

p {
  margin: 0;
}

.group-text-connect {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

.price-info {
  font-size: 14px;
  margin: -5px 0px 15px 0px;
  font-weight: 600;
}

.header-content-support {
  font-size: 30px;
  font-weight: 700;
  text-align: center;
}

.button-switch {
  margin-top: 15px;
  font-size: 25px;
}

.custom-button {
  border-radius: 5px;
}

.button-content {
  margin-top: 15px;
  margin-right: 20px;
  font-size: 25px;
}

.container-app {
  text-align: center;
}

.header-button {
  display: flex;
  justify-content: flex-end;
  padding: 10px 10px 0;
}

.group-container {
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
}

.group-header {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 10px 0 10px 0;
  gap: 10px;
}

.group-content {
  margin-top: 30px;
}

.heading-title {
  padding-top: 15px;
  padding-bottom: 10px;
  font-weight: 700;
}

.btn-connect {
  border: 2px solid white;
  color: white;
}

.btn-grad {
  border: 2px solid white;
  color: white;
  width: 90px;
  background-color: #0f3460;
  font-size: 15px;
  font-weight: 600;
}

.english-content {
  font-family: "NES-TTF";
  padding-bottom: 20px;
  margin: 0 20px 0 20px;
  text-align: justify;
  font-weight: 600;
}

.japan-content {
  font-family: "PixelMplus12";
  margin: 0 20px 0 20px;
  text-align: justify;
  font-weight: 600;
}

.group-footer-icon {
  display: flex;
  align-items: center;
  justify-content: center;

  gap: 15px;
}
.group-footer-icon a {
  background-color: #fff;
  display: grid;
  place-items: center;
  padding: 6px;
  border-radius: 50%;
}

.footer-content {
  padding-top: 10px;
  font-weight: 700;
}

.heading-content {
  font-weight: 600;
}

.team-role {
  font-size: 13px;
  font-weight: 500;
}

.english-content {
  font-family: "NES-TTF";
  padding-bottom: 20px;
  text-align: justify;
  font-weight: 600;
}

.japan-content {
  font-family: "PixelMplus12";
  text-align: justify;
  font-weight: 600;
}

.group-image {
  display: flex;
  padding: 10px 0 20px 0;
  justify-content: space-between;
}

.btn-custom {
  margin: 0;
  padding: 0;
  background-color: #fff;
}

.address-text {
  padding-top: 4px;
  margin-right: 15px;
}

@media only screen and (min-width: 768px) and (max-width: 1023px) {
  .group-image {
    margin: 0 150px 0 150px;
  }

  .content-space {
    margin: 0 100px 0 100px;
  }

  .team-image {
    width: 80px;
    height: 80px;
  }
}

@media only screen and (min-width: 1024px) {
  .group-image {
    margin: 0 250px 0 250px;
  }

  .content-space {
    margin: 0 160px 0 160px;
  }

  .team-image {
    width: 80px;
    height: 80px;
  }
}

@media only screen and (max-width: 767px) {
  .group-image {
    margin: 0 50px 0 50px;
  }

  .content-space {
    margin: 0 30px 0 30px;
  }

  .team-image {
    width: 60px;
    height: 60px;
  }

  .address-text {
    width: 40%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    margin-right: 10px;
  }
}
